    --bottom-line: 0.5px solid #000;
}

* {
    margin: 0;
    padding:0;
    box-sizing: border-box;
    overflow: hidden;
    font-family: system-ui, sans-serif;
}


body {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
}

header {
    flex-shrink: 0;
    color: #fff;
    height: 64px;
    display: flex;
    padding-left: 56px;
    background-color: #81C784;
}

.logo {
    display: flex;
    gap: 16px;
    align-items: center;
}

.logo > .text {
    font-size: 30px;
    font-weight: 600;
}

.logo-icon > img {
    width: 40px;
}

.main {
    --font-size: 1.1em;
    --icon-widht: 24px;
    flex-grow: 1;
    max-height: calc(100% - 64px);
    display: flex;
}

.main .left {
    --font-weight: 400;
    --left-padding: 16px;
    width: 300px;
    padding: 16px var(--left-padding) ;
    display: grid;
    grid-template-rows: 300px 1fr 64px;
    overflow-y: auto;
    background-color: rgb(249, 249, 249);
}

.task-classifications {
    display: grid;
}

.task-classifications > div ,
.projects .projects-list > div{
    font-size: var(--font-size);
    font-weight: 500;
    background-color: #fff;
    margin: 6px 6px; 
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 0 12px;
    cursor: pointer;
    border-radius: 4px;
    border: 0.2px solid #fff;
    transition: 0.3s;
}

.task-classifications > div:hover ,
.projects .projects-list > div:hover ,
.todos:hover {
    border: 0.2px solid #25b02c;
}

.task-classifications .icon , 
.projects-list .icon ,
.todos div:nth-child(2) .icon {
    width: var(--icon-widht);
}

.left .projects {
    display: flex;
    flex-direction: column;
    margin-top: 16px;
}

.projects .head ,
.todos-container .head {
    display: flex;
    align-items: end;
    justify-content: space-between;
    padding-bottom: 8px;
    border-bottom: var(--bottom-line);
    height: 36px;
}

.projects .head {
    padding-right: var(--left-padding);
}

.head .title {
    font-size: calc(var(--font-size) + 6px);
}

.head button {
    padding: 3px 8px;
    background-color: rgb(87, 86, 86);
    color: #fff;
    border: none;
    font-size: 16px;
    font-weight: 600;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.projects-list {
    margin : 12px 0;
    display: grid;
    grid-auto-rows: 58px;
}

.projects-list > div {
    justify-content: space-between;
}

.projects-list .icons-container {
    height: 100%;
    width: 64px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.left a {
    color: #25b02c;
    text-decoration: none;
    font-weight: var(--font-weight);
}

.left .footer {
    text-align: center;
    border-top: var(--bottom-line);
}
/* style for right side (main section) */
.main .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 48px;
    padding-top: 32px;
}

.todos-container  {
    --padding-left-right: 24px;
    width: 75%;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.todos-container .head {
    padding-left: var(--padding-left-right);
    padding-right: var(--padding-left-right);
}

.project-name {
    font-size: 3em;
}

.icons-container img:hover , 
.todos div:nth-child(2) img:hover {
    margin-bottom: 4px;
}

.todos-list {
    flex-grow: 1;
    display: grid;
    grid-auto-rows: 56px;
    row-gap: 1px;
}

.todos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 32px;
    border: 0.2px solid #fff;
    transition: 0.3s;
    cursor: pointer;
}

.todos > div {
    display: flex;
    align-items: center;
}

.achievement {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
}
/* style for task's priority */
.notImportant {
    background-color: #ADD8E6;
}

.aBitImportant {
    background-color: #FFD700;
}

.veryImportant {
    background-color: #da4328;
}

.achievement.icon img {
    width: 14px;
}
/* for completed tasks */
.done {
    text-decoration: line-through;
    opacity: 0.25;
}

.todos .icon ,
.todos .date{
    margin: 0 8px;     
    cursor: pointer;
}

.date {
    font-size: 0.9em;
    color:#25b02c;
}
/* style for dialog */

dialog[open] {
    width: 400px;
    max-height: 600px;
    margin: 120px auto 60px auto;
    border: 1px solid #fff;
}

dialog[open]::backdrop {
    background-color: rgb(0 0 0 / 25%);
}

dialog[open] .head {
    display: flex;
    width: 100%;
    height: 64px;
    padding: 8px 16px;
    justify-content: space-between;
    align-items: center;
    background-color: #25b02c;
    color: #fff;
}

dialog[open] .head.delete {
    background-color: #da4328;
}

dialog[open] .head .title {
    font-size: 1.6em;
    font-weight: 600;
}

.head a {
    align-self: flex-start;
    background-color: #fff;
    border-radius: 50%;
    border: none;
    padding: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.head a img{
    width: 14px;
    color: #fff;
}


dialog[open] form {
    display: flex;
    flex-direction: column;
    margin: 1em 2em 0 2em; 
    gap: 1em;
}

dialog[open] form p {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

dialog[open] form p + p {
    margin-top: 16px;
}

.inputs-div input,
[name="todos_priority"] , 
textarea {
    height: 40px;
    padding: 0 12px;
}

.inputs-div input, 
select, 
textarea {
    font-size: 1em;
    width: 100%;
}

.inputs-div textarea {
    max-width: 100%;
    max-height: 100px;
    min-height: 40px;
}
.btns-div {
    border-top: var(--bottom-line);
    padding: 1.5em 0;
    display: flex;
    justify-content: flex-end;
    gap:16px;
}

.btns-div button {
    padding: 8px 24px;
    border: none;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
}
